import React from 'react'
import TopBlockWithImage from '../../../components/TopBlockWithImage/TopBlockWithImage'

import styles from './OnlineStudy.module.scss'
import topImg from '../../../img/online-study/online-study-top-img.png'
import SkewedBlock from '../../../components/SkewedBlock/SkewedBlock'
import AdaptiveParagraphs from '../../../components/AdaptiveParagraphs/AdaptiveParagraphs'
import ParagraphWithTitle from '../../../components/AdaptiveParagraphs/ParagraphWithTitle/ParagraphWithTitle'

const OnlineStudy = (props) => {

  const osAdvantagesList = [
    {
      title: '  ',
      text: '   3     ;'
    },
    {
      title: '  24/7 ',
      text: '  VEPAY      ;'
    },
    {
      title: '    ',
      text: '      -        SMS;'
    },
    {
      title: '  ',
      text: ',     ,     .  ,   ,   24/7;'
    },
    {
      title: ' Anti-fraud  ',
      text: '   ;'
    },
    {
      title: '  ',
      text: '        .     ;'
    },
    {
      title: '  ',
      text: '     PCI DSS;'
    },
    {
      title: '   ',
      text: '     Viber, WhatsApp, Telegram, Facebook Messenger;'
    },
    {
      title: '  ',
      text: '   .        ;'
    },
  ]

  const vepayPossibilities = [
    '  ()',
    '   ( ,   ,  ,   ,  ,    QR   )',
    ' ',
    '',
    '  ()',
    '      ',
    '  ',
    '-    ',
    '     ',
    '   ',
  ]

    return (
        <div className={styles.OnlineStudy}>

            <TopBlockWithImage
              topBlockImg={topImg}
              topBlockItemText1='   -'
              topBlockItemText2=' VEPAY          .'
            />

            <SkewedBlock>
              <h1> VEPAY</h1>
              <AdaptiveParagraphs>
                {
                  osAdvantagesList.map(item => <ParagraphWithTitle {...item} key={item.title}/>)
                }

              </AdaptiveParagraphs>
            </SkewedBlock>

            <div className={styles.possibilitiesBlock}>
                <h1> VEPAY</h1>
              <AdaptiveParagraphs>
                {
                  vepayPossibilities.map((item, id) => <p key={id}>{item}</p>)
                }
              </AdaptiveParagraphs>
            </div>
        </div>
    )
}

export default OnlineStudy