import React from 'react'

import styles from './ReceivingPayments.module.scss'
import SkewedBlock from '../../components/SkewedBlock/SkewedBlock'
import TextWithTick from '../../components/UI/TextWithTick/TextWithTick'
import SolutionsCard from '../../components/Cards/SolutionsCard/SolutionsCard'
import topImg from '../../img/receiving-payments/paymentReceivingTopImg.png'
import topImgNarrow from '../../img/receiving-payments/paymentReceivingTopImg2.png'
import solutionImg1 from '../../img/receiving-payments/solutionImg1.png'
import solutionImg2 from '../../img/receiving-payments/solutionImg2.png'
import solutionImg3 from '../../img/receiving-payments/solutionImg3.png'
import solutionImg4 from '../../img/receiving-payments/solutionImg4.png'
import solutionImg5 from '../../img/receiving-payments/solutionImg5.png'
import solutionImg6 from '../../img/receiving-payments/solutionImg6.png'
import solutionImg7 from '../../img/receiving-payments/solutionImg7.png'
import solutionImg8 from '../../img/receiving-payments/solutionImg8.png'
import solutionImg9 from '../../img/receiving-payments/solutionImg9.png'
import TopBlockWithImageNEW from '../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'

const ReceivingPayments = (props) => {

    const tickedTextList1 = [
        '    3     ; ',
        '   - ; ',
        ' Anti-fraud ,   ;',
       
    ]

    const tickedTextList2 = [
        'VEPAY    PCI DSS;',
        '       ; ',
        '  24/7',
    ]

    const solutionsList = [
        {
            header: '  ',
            text: '      .       .',
            image: solutionImg1
        },
        {
            header: 'QR-',
            text: ' / /  /   QR-.   QR-,           ,   .',
            image: solutionImg2
        },
        {
            header: ' ',
            text: '      .     -.         .',
            image: solutionImg3
        },
        {
            header: 'WeChat',
            text: '       .        .   WeChat    QR-.',
            image: solutionImg4
        },
        {
            header: ' ',
            text: '        .        .',
            image: solutionImg5
        },
        {
            header: '  ',
            text: '      .      .     .',
            image: solutionImg6
        },
        {
            header: ' ',
            text: '    (Mastercard, Visa, ).      PCI DSS level 1.',
            image: solutionImg7
        },
        {
            header: '',
            text: '      .               .',
            image: solutionImg8
        },
        {
            header: '  ',
            text: '  ,     PIN-     .',
            image: solutionImg9
        },
    ]

    return (
        <div className={styles.ReceivingPayments}>
            <TopBlockWithImageNEW
                topBlockImg={topImg}
                topBlockImgNarrow={topImgNarrow}
                topBlockItemText1={'    '}
                topBlockItemText2={'  ,      .   :  -,   .   .  :  ,        .'}
            />

            <SkewedBlock>

                <h1> VEPAY:</h1>

                <div className={styles.tickedBlocks}>
                    <div className={styles.tickedBlock1}>
                        {
                            tickedTextList1.map(text => <TextWithTick text={text} type={'green'} key={text}/>)
                        }
                    </div>
                    <div className={styles.tickedBlock2}>
                        {
                            tickedTextList2.map(text => <TextWithTick text={text} type={'green'} key={text}/>)
                        }
                    </div>
                </div>


            </SkewedBlock>


            <div className={styles.solutionsBlock}>

                {
                    solutionsList.map(item => <SolutionsCard {...item} key={item.header}/>)
                }
             

            </div>
        </div>
    )
}

export default ReceivingPayments