import React from 'react'
import TopBlockWithImage from '../../../components/TopBlockWithImage/TopBlockWithImage'

import styles from './Housing.module.scss'
import topImg from '../../../img/housing/housing-top-img.png'
import SkewedBlock from '../../../components/SkewedBlock/SkewedBlock'
import AdaptiveParagraphs from '../../../components/AdaptiveParagraphs/AdaptiveParagraphs'
import ParagraphWithTitle from '../../../components/AdaptiveParagraphs/ParagraphWithTitle/ParagraphWithTitle'

const Housing = (props) => {

  const housingPayments = [
    {
      title: '  ',
      text: '        - .'
    },
    {
      title: '  ',
      text: '          .    (   ).'
    },
    {
      title: '  ',
      text: '      -        SMS.'
    },
    {
      title: '  ',
      text: '          AFT.            .      AFT , VEPAY    ,    .'
    },
    {
      title: '  ',
      text: ' SDK             QR-.'
    },
  ]

    return (
        <div className={styles.Housing}>
            <TopBlockWithImage
              topBlockImg={topImg}
              topBlockItemText1='       '
              topBlockItemText2='       .'
            />

            <SkewedBlock>
              <AdaptiveParagraphs>
                {
                  housingPayments.map(item => <ParagraphWithTitle {...item} key={item.title} />)
                }
              </AdaptiveParagraphs>
            </SkewedBlock>
        </div>
    )
}

export default Housing